// 设置 rem 函数
function setRem () {
  // if (!isMobile) {
  //   return
  // }
  // 当前页面宽度相对于 750 宽的缩放比例，可根据自己需要修改。
  // const scale = document.documentElement.clientWidth / 750
  // // 设置页面根节点字体大小
  // document.documentElement.style.fontSize = (baseSize * Math.min(scale, 2)) + 'px'

  // 移动端以750设计稿为准 1rem=100px
  // pc端以1920-1200设计稿为准 1rem=100px
  var deviceWidth = document.documentElement.clientWidth || window.innerWidth
  console.log('##setRem1:', deviceWidth)
  if (deviceWidth < 768) {
    // 移动端显示方案
    // document.documentElement.style.fontSize = (deviceWidth * 2 / 7.5) + 'px'
    document.documentElement.style.fontSize = (deviceWidth / 7.5) + 'px'
    console.log('##setRem2:', document.documentElement.style.fontSize)
  } else if (deviceWidth >= 1280) {
    // 标准1280显示方案
    document.documentElement.style.fontSize = 100 + 'px'
    console.log('##setRem3:', document.documentElement.style.fontSize)
  } else if (deviceWidth >= 768 && deviceWidth < 1024) {
    document.documentElement.style.fontSize = 72.0 / 120 * 100 + 'px'
    // max-width:720px;
    console.log('##setRem4:', document.documentElement.style.fontSize)
  } else if (deviceWidth >= 1024 && deviceWidth < 1280) {
    document.documentElement.style.fontSize = 96.0 / 120 * 100 + 'px'
    // max-width:960px;
    console.log('##setRem5:', document.documentElement.style.fontSize)
  }
}
// 初始化
// setMeta()
setRem()

// 改变窗口大小时重新设置 rem
window.onresize = function () {
  // setMeta()
  setRem()
}



